import ctx from './ctx';
const { Textcomplete, Textarea } = require('textcomplete');
ctx.addHandler('textarea.BBCodeField', activeerLidHints);
export function activeerLidHints(textarea) {
    const editor = new Textarea(textarea);
    const textcomplete = new Textcomplete(editor);
    textcomplete.register([{
            match: /(^|\s|])@((?:[^ ]+ ?){1,5})$/,
            replace(data) {
                return '$1[lid=' + data.label + ']';
            },
            search,
            template,
        }, {
            index: 3,
            match: /(^|\s|])\[(citaat|lid)=(?:[0-9]{4}|([^\]]+))$/,
            search,
            template,
            replace(data) {
                return '$1[$2=' + data.label;
            },
        }]);
    textcomplete.on('rendered', () => {
        if (textcomplete.dropdown.items.length >= 1) {
            textcomplete.dropdown.items[0].activate();
        }
    });
}
function search(term, callback) {
    if (!term || term.length === 1) {
        callback([]);
    }
    else {
        $.ajax('/tools/naamsuggesties?vorm=user&zoekin=voorkeur&q=' + encodeURI(term))
            .done((data) => {
            callback(data);
        })
            .fail(() => {
            callback([]);
        });
    }
}
function template(data, term) {
    return data.value;
}
