import $ from 'jquery';
import ctx, { init } from '../ctx';
import { parseData } from '../util';
import render from './render';
ctx.addHandler('.ctx-datatable', initDataTable);
ctx.addHandler('.ctx-offline-datatable', initOfflineDataTable);
async function initDataTable(el) {
    await import('./bootstrap');
    const $el = $(el);
    const settingsJson = $el.data('settings');
    const search = $el.data('search');
    if (settingsJson.ajax) {
        settingsJson.ajax.data.lastUpdate = fnGetLastUpdate($el);
        settingsJson.ajax.dataSrc = fnAjaxUpdateCallback($el);
    }
    settingsJson.columns.forEach((col) => col.render = render[col.render]);
    const table = $el.DataTable(settingsJson);
    $el.dataTable().api().search(search);
    table.on('page', () => table.rows({ selected: true }).deselect());
    table.on('childRow.dt', (event, data) => init(data.container.get(0)));
}
async function initOfflineDataTable(el) {
    await import('./bootstrap');
    $(el).DataTable(parseData(el));
}
export function fnUpdateDataTable(tableId, response) {
    const $table = $(tableId);
    const table = $table.DataTable();
    response.data.forEach((row) => {
        const $tr = $('tr[data-uuid="' + row.UUID + '"]');
        if ($tr.length === 1) {
            if ('remove' in row) {
                table.row($tr).remove();
            }
            else {
                table.row($tr).data(row);
                init($tr.get(0));
            }
        }
        else if ($tr.length === 0) {
            table.row.add(row);
        }
        else {
            alert($tr.length);
        }
    });
    table.draw(false);
}
export function fnGetSelection(tableId) {
    const selection = [];
    $(tableId + ' tbody tr.selected').each(function () {
        selection.push($(this).attr('data-uuid'));
    });
    return selection;
}
export function getApiFromSettings(settings) {
    return new $.fn.dataTable.Api(settings);
}
function fnAutoScroll($table) {
    const $scroll = $table.parent();
    if ($scroll.hasClass('dataTables_scrollBody')) {
        if ($scroll.scrollTop() + $scroll.innerHeight() >= $scroll[0].scrollHeight - 20) {
            window.setTimeout(() => {
                $scroll.animate({
                    scrollTop: $scroll[0].scrollHeight,
                }, 800);
            }, 200);
        }
    }
}
export const fnGetLastUpdate = ($table) => () => Number($table.data('lastupdate'));
export const fnSetLastUpdate = ($table) => (lastUpdate) => $table.data('lastupdate', lastUpdate);
export const fnAjaxUpdateCallback = ($table) => (json) => {
    fnSetLastUpdate($table)(json.lastUpdate);
    const tableConfig = $table.DataTable();
    if (json.autoUpdate) {
        const timeout = parseInt(json.autoUpdate, 10);
        if (!isNaN(timeout) && timeout < 600000) {
            setTimeout(() => {
                $.post(tableConfig.ajax.url(), {
                    lastUpdate: fnGetLastUpdate($table),
                }, (data) => {
                    fnUpdateDataTable($table.attr('id'), data);
                    fnAjaxUpdateCallback($table)(data);
                });
            }, timeout);
        }
    }
    fnAutoScroll($table);
    return json.data;
};
export function replacePlaceholders(action, row) {
    const replacements = /:(\w+)/g.exec(action);
    if (!replacements) {
        return action;
    }
    for (let i = 1; i < replacements.length; i++) {
        action = action.replace(':' + replacements[i], row[replacements[i]]);
    }
    return action;
}
