import axios from 'axios';
import $ from 'jquery';
import './bbcode';
import { init } from './ctx';
import './formulier';
import './fotoalbum/main';
require('lightbox2');
require('./lib/jquery.markitup');
require('jquery-ui/ui/widgets/tooltip');
require('jquery-hoverintent');
require('timeago');
const $window = $(window);
const $header = $('#header');
const $banner = $('#banner');
$window.on('load', () => {
    setTimeout(() => {
        $('div.bb-img-loading').each(function () {
            const content = $(document.createElement('img'));
            content.on('error', function () {
                $(this).attr('title', 'Afbeelding bestaat niet of is niet toegankelijk!');
                $(this).attr('src', '/plaetjes/famfamfam/picture_error.png');
                $(this).css('width', '16px');
                $(this).css('height', '16px');
                $(this).removeClass('bb-img-loading').addClass('bb-img');
            });
            content.addClass('bb-img');
            content.attr('alt', $(this).attr('title'));
            content.attr('style', $(this).attr('style'));
            content.attr('src', $(this).attr('src'));
            $(this).html(content.toString());
            content.on('load', function () {
                const foto = content.attr('src').indexOf('/plaetjes/fotoalbum/') >= 0;
                const video = $(this).parent().parent().hasClass('bb-video-preview');
                const hasAnchor = $(this).closest('a').length !== 0;
                $(this).parent().replaceWith($(this));
                if (!foto && !video && !hasAnchor) {
                    $(this).wrap(`<a class="lightbox-link" href="${$(this).attr('src')}" data-lightbox="page-lightbox"></a>`);
                }
            });
        });
    });
});
const lazyLoad = (() => {
    let hasLoaded = false;
    return () => {
        if (hasLoaded) {
            return;
        }
        hasLoaded = true;
        setTimeout(() => {
            $('.lazy-load').each(function () {
                $(this).replaceWith(this.textContent);
            });
        });
    };
})();
$window.on('scroll', () => {
    if ($(window).scrollTop() > 0) {
        lazyLoad();
    }
    if (window.pageYOffset > $banner.outerHeight()) {
        $header.removeClass('alt');
    }
    else {
        $header.addClass('alt');
    }
});
$window.on('resize', () => $window.trigger('scroll'));
$window.trigger('scroll');
init(document.body);
const contactForm = document.querySelector('#contact-form');
if (contactForm) {
    const errorContainer = document.querySelector('#melding');
    const submitButton = contactForm.submitButton;
    contactForm.addEventListener('submit', (event) => {
        event.preventDefault();
        errorContainer.innerHTML = '';
        submitButton.disabled = true;
        const formData = new FormData(contactForm);
        axios.post('/contactformulier/interesse', formData)
            .then((response) => {
            contactForm.reset();
            submitButton.disabled = false;
            errorContainer.innerHTML = '<div class="alert alert-success">' +
                '<span class="ico accept"></span>' + response.data +
                '</div>';
        })
            .catch((error) => {
            submitButton.disabled = false;
            errorContainer.innerHTML = '<div class="alert alert-danger">' +
                '<span class="ico exclamation"></span>' + error.response.data +
                '</div>';
        });
        return false;
    });
}
