import axios from 'axios';
import './bbcode-hints';
import ctx, { init } from './ctx';
ctx.addHandler('[data-bbpreview-btn]', (el) => {
    const previewId = el.dataset.bbpreviewBtn;
    const source = document.querySelector('#' + previewId);
    const target = document.querySelector('#preview_' + previewId);
    if (!source || !target) {
        throw new Error('Bbpreview van niet bestaande elementen');
    }
    el.addEventListener('click', () => CsrBBPreviewEl(source, target));
});
ctx.addHandler('[data-bbpreview]', (el) => {
    const previewId = el.dataset.bbpreview;
    const target = document.querySelector('#preview_' + previewId);
    if (!target) {
        throw new Error('Geen target gevonden voor bbpreview');
    }
    el.addEventListener('keyup', (event) => {
        if (event.key === 'Enter') {
            CsrBBPreviewEl(el, target);
        }
    });
});
export const CsrBBPreviewEl = (source, target, params = {}) => {
    const bbcode = source.value;
    if (bbcode.trim() === '') {
        target.innerHTML = '';
        target.style.display = 'none';
        return;
    }
    axios.post('/tools/bbcode', Object.assign({ data: encodeURIComponent(bbcode) }, params)).then((response) => {
        target.innerHTML = response.data;
        init(target);
        target.style.display = 'block';
    }).catch((error) => {
        alert(error);
    });
};
